.grid-container-element {
  display: grid;
  grid-template-columns: 6fr 9fr;
  grid-gap: 0px;

  width: 100%;
  height: 100vh;
}
.grid-child-element {
  object-fit: fill;
}
.childimage {
  width: 100%;
  height: 100vh;
}
.grid-child-element.purple {
  text-align: center;
}
.content {
  /* background: pink; */
  font-size: 200%;
  height: 290px;
  margin-top: 290px;
}
.redirect-button {
  height: 100px;
}

.button-46 {
  align-items: center;
  background-color: rgba(240, 240, 240, 0.26);
  border: 1px solid #dfdfdf;
  /* border-radius: 16px; */
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  font-size: 28px;
  justify-content: center;
  line-height: 28px;
  max-width: 100%;
  padding: 14px 22px;
  text-decoration: none;
  transition: all 0.2s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 40%;
}

.button-46:active,
.button-46:hover {
  outline: 0;
}

.button-46:hover {
  color: goldenrod;
  background-color: #ffffff;
  border-color: rgba(0, 0, 0, 0.19);
}

@media (min-width: 768px) {
  .button-46 {
    font-size: 20px;
    min-width: 200px;
    padding: 14px 16px;
  }
}
@font-face {
  font-family: "luxury2";
  src: url(/din/fonts/FF\ DIN\ Pro\ Regular\ 2.otf);
}
.reside {
  font-family: "luxury2";
}
.amenity-logo {
  height: 100px;
}
.logo-top-left {
  position: absolute;
  top: 15px;
  left: 28px;
}
.logo-top-left img {
  height: 70px;
}
.logo-top-left a {
  text-decoration: none;
  /* color: black; */
  font-size: 30px;
  margin-left: 20px;
  position: absolute;
  top: 20px;
  left: 66px;
  width: 150px;
  color: #7b7471;
}
.bars-top-right {
  /* background-color: purple; */
  position: absolute;
  top: 10px;
  right: 59px;
  transform: scale(2.9, 1.5);
  font-size: 18px;
  margin-top: 8px;
  color: white;
}
.residenceimagehome {
  object-fit: cover;
}
@media all and (max-width: 700px) {
  .logo-top-left {
    position: absolute;
    top: 15px;
    left: 18px;
  }
  .logo-top-left img {
    height: 50px;
  }
  .logo-top-left a {
    text-decoration: none;
    font-size: 129%;
    position: absolute;
    top: 15px;
    left: 20px;
    width: 150px;
    color: #7b7471;
  }
  .grid-container-element.homepager {
    display: block;
  }
  .grid-container-neighbor.homepager {
    display: block;
  }
  .bars-top-right {
    position: absolute;
    top: 29px;
    right: 40px;
    transform: scale(2.9, 1.5);
    font-size: 15px;
    margin-top: 8px;
    color: black;
  }
  .grid-container-element {
    display: grid;
    grid-template-columns: 7fr 6fr;
    grid-gap: 0px;
    width: 100%;
    height: 100vh;
    background-color: grey;
    margin: 0 0 0 0;
  }
  .content.jirani {
    background-color: white;
  }
  h4.jirani-main {
    color: rgb(98, 92, 92);
  }
  h5.jirani-paragraph {
    color: rgb(98, 92, 92);
  }

  .navbary-loogo {
    height: 67px;
    border-radius: 90px;
    margin-bottom: 10px;
  }
  .reside.style {
    font-family: "luxury2";
    font-size: 70%;
    color: #7b7471;
    margin-bottom: 15px;
    margin-top: 15px;
    /* background-color: peru; */
  }
  .reside-paragraph.disc {
    font-family: "luxury";
    font-weight: normal;
    text-align: center;
    /* background-color: brown; */
    color: #7b7471;
    font-size: 60%;
  }
  .content {
    /* background: pink; */

    height: 350px;
    margin-top: 65%;
  }

  .button-46 {
    font-family: "luxury";
    font-size: 60%;
    min-width: 200px;
    padding: 14px 16px;
  }
}

